import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	adaptCallbackForLegacyAPI,
	isAttrTrue,
	transformMessages
} from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import '@servicenow/now-input';

import styles from './ip-address.scss';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const getError = value => ({
	type: 'error',
	icon: 'circle-close-outline',
	message: t('{0} is not a valid IP address', value)
});

// Dispatches value change
const dispatchValueChanged = (payload, effectProps) => {
	const { state, updateState, dispatch } = effectProps;
	const onValueChange = state.properties.onValueChange;
	const { value, error } = payload;
	const shouldDispatch =
		state.properties.value !== value || state.error == error;

	const errorNotReadOnly = isAttrTrue(state.properties.readonly)
		? false
		: error;

	updateState({ error: errorNotReadOnly });
	if (onValueChange) {
		shouldDispatch &&
			adaptCallbackForLegacyAPI(onValueChange)({
				...payload,
				error: errorNotReadOnly
			});
	}

	shouldDispatch &&
		dispatch('NOW_RECORD_IP_ADDRESS#VALUE_CHANGED', {
			...payload,
			error: errorNotReadOnly
		});
};

const view = state => {
	const {
		disabled,
		messages,
		error,
		invalid,
		label,
		placeholder,
		name,
		required,
		readonly,
		autofocus,
		value
	} = state.properties;

	return (
		<now-input
			type="ip"
			autofocus={isAttrTrue(autofocus)}
			invalid={invalid || Boolean(error)}
			label={label}
			messages={transformMessages(messages)}
			name={name}
			placeholder={placeholder}
			readonly={isAttrTrue(readonly)}
			disabled={isAttrTrue(disabled)}
			required={isAttrTrue(required)}
			value={value}
			manage-value
			manage-invalid
		/>
	);
};

const COMPONENT_NAME = 'now-record-ip-address';

/**
 *
 *  Input field that follows the IP address format.
 *```jsx
 * <div>
 *	<now-record-ip-address
 *		label="I am label"
 *		value={state.value}
 *		placeholder={'10.1.11.111'}
 *	/>
 * </div>;
 *
 *```
 * @seismicElement now-record-ip-address
 * @summary *  Input field that contains IP address format constraints.
 * */
createCustomElement(COMPONENT_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		 * When true, the input will be focused immediately upon being rendered.
		 * @type {boolean}
		 */
		autofocus: {},
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * Disable the field so it can not be used or tabbed to
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Flag indicating whether there is an error in ip-address formatting
		 * @type {string | boolean }
		 */
		error: { default: '' },
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true. Used by the glide form controls demo
		 * @type {string | boolean}
		 * @private
		 */
		invalid: {},
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * The language for the character used in the input field
		 * @type {string}
		 */
		language: {},
		/**
		 * The messages that you want to display underneath the component
		 * @type {[{ type: 'string', message: 'string' }]}
		 */
		messages: {},
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * Callbacks for Forms consumption
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Content to display when there is no suitable display value.
		 * @type {string}
		 */
		placeholder: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {string | boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 * @private
		 */
		reversed: { default: false },
		/**
		 * Underlying data for the component on initial focus
		 * @type {string}
		 */
		value: { default: '' }
	},
	actions: {
		'NOW_INPUT#VALUE_SET': { stopPropagation: true }
	},
	actionHandlers: {
		'NOW_INPUT#INVALID_SET': {
			stopPropagation: true,
			effect: effectProps => {
				const { readonly } = effectProps.properties;
				if (!isAttrTrue(readonly)) {
					const {
						name,
						value: invalid,
						fieldValue: value
					} = effectProps.action.payload;
					const payload = {
						name,
						value,
						error: invalid ? getError(value) : false
					};

					dispatchValueChanged(payload, effectProps);
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched on blur.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_IP_ADDRESS#VALUE_CHANGED': {}
	}
});

export default COMPONENT_NAME;
